@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
body {
  margin: 0;
  font-family: "Baloo Paaji 2", cursive;
  line-height: 1.2;
}

@-webkit-keyframes color-change{0%{background:#ff414e}100%{background:#ffde22}}@keyframes color-change{0%{background:#ff414e}100%{background:#ffde22}}#root{display:flex;height:100vh;justify-content:center;align-items:center;background:#ffde22}.app{text-align:center;background:#ff8928;width:500px;display:block;border-radius:3px;justify-content:center;align-items:center}.app header{display:block;text-align:left;width:400px;align-items:center;margin:30px auto 0 auto}.app .app-display{height:20px;width:200px;margin:0 auto;background:#f46e00;border-radius:3px}.app .power-button{display:flex;align-items:center;justify-content:center;margin:20px auto;cursor:pointer;background:#ff414e;width:60px;height:30px;border-radius:3px;box-shadow:3px 3px 3px #8e4000}.app .drum-pad-set{background:#f46e00;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;align-content:center;margin:10px auto;width:400px;height:400px;border-radius:3px}.app .drum-pad-set .drum-pad{width:120px;height:120px;cursor:pointer;margin:5px;display:flex;align-items:center;justify-content:center;font-size:2em;background:#000;border-radius:3px;box-shadow:3px 3px 3px #8e4000}.app .drum-pad-set .drum-pad:active{background:#ff414e;-webkit-animation-name:color-change;animation-name:color-change;-webkit-animation-duration:0.1s;animation-duration:0.1s}.app .footer{float:right;font-size:0.7em;margin:2px 5px}@media screen and (max-width: 650px){.app{transform:scale(0.6)}}

